font-face{
    font-family: "Segoe UI Symbol", serif;
    font-family: "Rockwell Extra Bold", serif;
}

.ImageBox
{
    border-radius: 20px;
    margin-bottom: 10px;
    background-size: cover;
    transition: all ease-in-out 100ms;
    z-index: 2;
}
.sec{
 margin: -0.2px;
}
.ImageBox:hover{
    box-shadow: 0px 26px 50px 8px rgba(0, 0, 0, 0.6);
    -webkit-transform: translate(0, -1px);
            transform: translate(0, -1px);
    -webkit-animation-direction: alternate;
            animation-direction: alternate;
    background-color: rgba(198,200,182,0.22);
}

.mainText{
    display: block;
    color: white;
    float: bottom;
    margin-top: 10px;
    display: inline-block;
    position: relative;
    margin-top: 39vh;
    font-family: "Droid Sans Mono Dotted", serif;
    margin-left: 4vh;
    margin-bottom: 4vh;
}
.linkstyle{
    color: white;
    font-family: 'Helvetica', sans-serif;
    font-size: 2.7vw;
    text-decoration: none;
}
.i{
    width: 79vw;
    float: bottom;
    background-size: cover;
    margin-left: 10%;
    height: 50vh;
    background-position: center;
    margin-bottom: 2%;
    margin-top: 1%;
}


.Bar{
    position: fixed;
    position: static;
    font-family: "Roboto Condensed";
    width: 99%;
    height: 10%;
    padding: 0.1%;
    text-align: center;
    margin-bottom:1%;
    text-decoration: none;
    position: center;
    box-shadow: 4px 94px 300px -61px rgba(0,0,0,0.75);

}

.point{
    margin: 6vmin;
    font-size: 5vmin;
    text-align: center;
    position: center;
    text-decoration: none;
    color: #212121;
}

.box{
    display: flex;
    justify-content: center;
}
.header-style{
    text-align: center;
    margin-right: 40%;
font-family: "Roboto";
font-size: 350%;
    display: block;
    position: center;
    color: #1A1A1A;
    margin-bottom: 2%;
}
.newspage-div{
justify-content: center;
}

.img-style{
    border-radius: 3%;
    display: block;
    width: 70%;
    margin: auto;
    height: 10%;
    margin-bottom: 6%;
    position: center;
    image-resolution: from-image;
    box-shadow: 2px 9px 48px 2px rgba(0,0,0,0.31);
}
.img-style:hover
{
    box-shadow: 2px 9px 48px 2px rgba(0,0,0,0.51);
}
.img-style::after{
    background-color: black;
    width: 1000px;
    height: 1000px;
}

p{
    font-family: Roboto;
    padding-left: 20%;
    padding-right: 20%;
}
.Post-page{
    display:grid;
    grid-template-columns: 1fr;
    position: center;
    margin-left: 30vmin;


   justify-content: center;
}

.date{
    height: 100px;
    width: 100px;
    background-color: #61dafb;
}


footer{
    margin-top: 30vh;
    position: relative;
    background-color: rgb(45, 44, 44);
    font-family: "Roboto Condensed";
    font-size: 5vh;
    color: white;
    padding-top: 4vh;
    padding-bottom: 4vh;
    text-align: center;
}

.submite{
    margin-top: 5vh;
    margin-bottom: 10vh;
    position: center;
    padding: 2vmin;
    margin-left: 50%;
    background-color: white;
    border-radius: 10px;
}

.tag{
    font-family: "Roboto Condensed";
    color: #212121;
    margin: 1vw;
    font-size: 3vmin;
}
.tag-bar{
    justify-content: center;
    display: flex;
}
.Post{
    display: block;
    height: 10vh;
    width: 70vw;
    margin-bottom: 1%;
}
.point-style{
    border:  1px solid rgba(0, 0, 0, 0.27);
    border-radius: 10px;
    padding: 10px;
    height: 2vmin;
    margin-left: 30%;
    width: 30vmin;
}
.test-class{
    position: relative;
    display: grid;
    padding-left: 10%;
    padding-right: 10%;
    /*grid-template-columns: calc(100%/3) calc(100%/3) calc(100%/3);*/
    grid-template-columns: 1fr 1fr ;
    grid-column-gap: 2vh;
    grid-row-gap: 2vh;
}



@media screen and (max-width: 600px ) {
    .test-class{ grid-template-columns: 1fr;
        grid-row-gap: 0.3%;
    }

    .point {
        margin: 3vmin;
    }
}
.RichEditor-root {
    background: #fff;
    border: 1px solid #ddd;
    border-radius: 25px;
    font-family: 'Georgia', serif;
    font-size: 14px;
    padding: 15px;
}

.RichEditor-editor {
    border-top: 1px solid #ddd;
    cursor: text;
    font-size: 16px;
    margin-top: 10px;
}

.RichEditor-editor .public-DraftEditorPlaceholder-root,
.RichEditor-editor .public-DraftEditor-content {
    margin: 0 -15px -15px;
    padding: 15px;
}

.RichEditor-editor .public-DraftEditor-content {
    min-height: 100px;
}

.RichEditor-hidePlaceholder .public-DraftEditorPlaceholder-root {
    display: none;
}

.RichEditor-editor .RichEditor-blockquote {
    border-left: 5px solid #eee;
    color: #666;
    font-family: 'Hoefler Text', 'Georgia', serif;
    font-style: italic;
    margin: 16px 0;
    padding: 10px 20px;
}

.RichEditor-editor .public-DraftStyleDefault-pre {
    background-color: rgba(0, 0, 0, 0.05);
    font-family: 'Inconsolata', 'Menlo', 'Consolas', monospace;
    font-size: 16px;
    padding: 20px;
}

.RichEditor-controls {
    font-family: 'Helvetica', sans-serif;
    font-size: 14px;
    margin-bottom: 5px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.RichEditor-styleButton {
    color: #999;
    cursor: pointer;
    margin-right: 16px;
    padding: 2px 0;
    display: inline-block;
}

.RichEditor-activeButton {
    color: #5890ff;
}
